	<div class="mrt">
		<Echart :options="options" id="centreLeft1Chart11" height="220px" width="477px"></Echart>
	</div>
</template>

<script>
	import Echart from '@/common/echart'
	export default {
		data() {
			return {
				options: {},
				start: 0,
				end: 3
			};
		},
		components: {
			Echart,
		},
		props: {
			cdata: {
				type: Object,
				default: () => ({})
			},
		},
		watch: {
			//  cdata: {
			//    handler (newData) {
