			// }
			//  },
			//  yAxis: {
			// type: 'category',
			// inverse: true, // 倒叙
			// axisLabel: {
			//   color: '#1E90FF',
			//   fontSize: 16,
			//   formatter: (val) => {
			// 	return `${val}`;
			//   }
			// },
			// axisLine: {
			//   show: false // 轴线
			// },
			// axisTick: {
			//   show: false,// 刻度线
			// },
			// data: newData.xData
			//  },
			//  series: [
			// {
			//   name: 'Echarts',
			//   type: 'bar',
			//   barWidth: 20,
			//   showBackground: true,
			//   barMaxWidth: 20,
			//   barMinWidth: 5,
			//   itemStyle: {
			// 	normal: {
			// 		label:{
			// 			show:true,
			// 			borderRadius: [0, 10, 10, 0],
			// 			position: 'right',
			// 			barBorderRadius: 5,
			// 			color: '#fff',
